<!--  -->
<template>
    <transition enter-active-class="animated slideInRight" leave-active-class="animated slideOutRight">
        <div class="invoiceMsg">
            <!--  -->
            <div class="top">
                <label></label>
                <h1><span></span>{{available}}</h1>
            </div>
            <ul class="menuList">
                <router-link to="invoiceTrip" append>
                    <li class="item">
                        
                    </li>
                </router-link>
                <router-link to="invoiceMoney" append>
                    <li class="item">
                        
                    </li>
                </router-link>
                <router-link to="invoiceHistory" append>
                    <li class="item">
                        
                    </li>
                </router-link>
            </ul>
            <p class="labelMsg">
                
                <br> 1.
                <br> 2.
                <br> 3.
                <br> 4.
                <br> 5.
                <br> 6.,0592-963630</p>
        </div>
    </transition>
</template>
<script>
import { mapState } from 'vuex'
export default{
  name: 'invoice',
  mounted () {
    this.$store.dispatch('set_Available').then(() => {
      if (!this.available) {
        let toast = this.$createToast({
          type: 'warn',
          time: 1000,
          txt: '0'
        })
        toast.show()
      }
    })
    // this.$store.commit('SET_PICKTRIPLIST', [])
    // this.$store.commit('SET_COUNTMONEY', 0)
    this.$store.commit('RESETALL')
  },
  computed: mapState({
    available (state) {
      return state.available
    }
  })
}
</script>
<style lang='less'>
.invoiceMsg {
  position: fixed;
  overflow-y:scroll; 
  box-sizing: border-box;
  display: block;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  bottom: 0;
  background: #ffffff;
  animation-duration: .3s;
  .top {
    position: relative;
    box-sizing: border-box;
    padding: 30px 0;
    label {
      display: block;
      width: 100%;
      font-size: 14px;
      color: #9ea7b1;
      text-align: center;
    }
    h1 {
      font-size: 32px;
      text-align: center;
      line-height: 1.5;
      font-weight: bold;
      color: #00b5e6;
      span{
        font-size: 14px;
      }
    }
  }
  .menuList{
    position: relative;
    display: block;
    padding-left: 15px;
    border-top: .5px solid #f3f3f3;
    border-bottom: .5px solid #f3f3f3;
    a {
      display: block;
      &:not(:last-child) {
        border-bottom: .5px solid #f3f3f3;
      }
    }
    li {
      position: relative;
      display: block;
      height: 50px;
      line-height: 50px;
      font-size: 14px;
      color: #4b4d51; 
      &:after{
        content: '';
        position: absolute;
        border-right: 1px solid #c5c7ca; 
        border-bottom: 1px solid #c5c7ca;  
        width: 8px;
        height: 8px;  
        transform: rotate(-45deg);  
        top: 50%;
        margin-top: -4px;
        right: 15px;
      }
    }
  }
  .labelMsg{
    display: block;
    margin-top: 30px;
    padding: 0 15px;
    font-size: 12px;
    line-height: 1.6;
    color: #9ea7b1;
  }
}

</style>


