<template>
    <transition enter-active-class="animated slideInRight" leave-active-class="animated slideOutRight">
        <div class="wrap">
            <!--  -->
            <div class="content">
                <div class="form-item">
                    <label class="form-label"></label>
                    <div class="input-item">
                        <input type="number" placeholder="" v-model="countMoney">
                    </div>
                    <span></span>
                </div>
            </div>
            <!--  -->
            <p class="title">{{available}}300</p>
            <!--  -->
            <p class="title absbottom"><cube-button :disabled="!countMoney" @click="sure"></cube-button></p>
        </div>
    </transition>
</template>
<script>
import { mapState } from 'vuex'
export default{
  data () {
    return {
      countMoney: this.$store.state.countMoney || ''
    }
  },
  computed: mapState({
    available (state) {
      return state.available
    }
  }),
  watch: {
    // 
    countMoney (newval, oldval) {
      if (!newval) {
        return
      }
      let reg = /^[1-9]*[1-9][0-9]*$/
      if (!reg.test(newval) || newval.length > 7) {
        this.countMoney = oldval
      }
    }
  },
  methods: {
    sure () {
      if (this.countMoney >= 10000) {
        this.$store.commit('SET_INVOICETYPE', 2)
        let toast = this.$createToast({
          txt: '10000',
          type: 'warn',
          mask: true
        })
        toast.show()
      }
      if (this.countMoney > this.available) {
        let toast = this.$createToast({
          txt: '',
          type: 'warn',
          mask: true
        })
        toast.show()
        return
      }
      this.$store.commit('SET_TICKETTYPE', 2) // 
      this.$store.commit('SET_COUNTMONEY', parseInt(this.countMoney))
      this.$router.push('invoiceForm')
    }
  }
}
</script>
<style lang="less" scoped>
@import "../../assets/css/less/variable.less";
.wrap {
  position: fixed;
  width: 100%;
  height: 100%;
  bottom: 0;
  left: 0;
  background: #f3f3f3;
  animation-duration: .3s;
  p.title {
    padding: 5px 15px;
    font-size: 12px;
    line-height: 1.5;
    color: #a9abb0;
  }
  .content {
    padding-left: 15px;
    color: #4f5a67;
    font-size: 14px;
    background: #ffffff;
    .form-item {
      display: flex;
      position: relative;
      height: 50px;
      line-height: 50px;
      &:not(:last-child) {
        &:after {
          content: '';
          position: absolute;
          bottom: 0;
          width: 100%;
          height: .5px;
          background: #f3f3f3;
        }
      }
      label.form-label {
        width: 100px;
      }
      .input-item {
        flex-grow: 1;
        input {
          width: 100%;
          outline: none;
          &::-webkit-input-placeholder {
            color: #c5c7ca;
          }
        }
      }
      span{
        padding: 0 15px;
      }
    }
  }
  .absbottom{
    position: absolute;
    bottom: 15px;
    width: 100%;
    box-sizing: border-box;
  }
}

</style>>


