import state from './state'
import axios from '../ajax'
export default {
  /* ----------------------- */
  setLocalStorage (key, value) {
    if (window.localStorage) {
      var storage = window.localStorage
      if (typeof (value) !== 'string') { storage.setItem(key, JSON.stringify(value)) } else { storage.setItem(key, value) }
    } else {
      console.log('')
    }
  },
  getLocalStorage (key, type) {
    if (window.localStorage) {
      var storage = window.localStorage
      var value
      if (type === 'object') { value = JSON.parse(storage.getItem(key)) } else { value = storage.getItem(key) }
      return value
    } else {
      console.log('')
    }
  },
  delLocalStorage (key) {
    if (window.localStorage) {
      var storage = window.localStorage
      storage.removeItem(key)
    } else {
      console.log('')
    }
  },
  // uuid
  removeDup (array, idname) {
    let arr = {}
    arr = array.reduce(function (item, next) {
      if (!arr[next.uuid]) {
        arr[next.uuid] = true
        item.push(next)
      }
      return item
    }, [])
    // 
    if (idname) {
      arr = array.reduce(function (item, next) {
        if (!arr[next[idname]]) {
          arr[next[idname]] = true
          item.push(next)
        }
        return item
      }, [])
    }
    return arr
  },
  // 
  get_balance (page) {
    let promise = new Promise((resolve, reject) => {
      axios.post('/wechat/passenger/getBalance', {
        openId: this.getLocalStorage('openId')
      }).then((data) => {
        resolve(data)
      }).catch((err) => {
        reject(err)
      })
    })
    return promise
  },
  // 
  get_available (page) {
    let promise = new Promise((resolve, reject) => {
      axios.get('/wechat/userInvoice/available', {
        pageNum: page,
        openId: this.getLocalStorage('openId')
      }).then((data) => {
        resolve(data)
      }).catch((err) => {
        reject(err)
      })
    })
    return promise
  },
  // 
  get_tripList (page) {
    let promise = new Promise((resolve, reject) => {
      axios.get('/wechat/userInvoice/unBillingTrip', {
        pageNum: page,
        openId: this.getLocalStorage('openId')
      }).then((data) => {
        resolve(data)
      }).catch((err) => {
        reject(err)
      })
    })
    return promise
  },
  // 
  get_MyCouponList (page) {
    let promise = new Promise((resolve, reject) => {
      axios.get('/wechat/coupon/listMyCouponMap', {
        status: 1,
        pageNum: page,
        openId: this.getLocalStorage('openId')
        // openId: 'oukrA1dbbITe9sJSdTNW6JrARHL4'
      }).then((data) => {
        resolve(data)
      }).catch((err) => {
        reject(err)
      })
    })
    return promise
  },
  // 
  get_invoiceHistory (page) {
    let promise = new Promise((resolve, reject) => {
      axios.post('/wechat/userInvoice/invoice/history', {
      // axios.post('/wechat/userInvoice/electron/history', {
        pageNum: page,
        openId: this.getLocalStorage('openId')
      }).then((data) => {
        resolve(data)
      }).catch((err) => {
        reject(err)
      })
    })
    return promise
  },
  // 
  post_invoice () {
    let promise = new Promise((resolve, reject) => {
      axios.post('/wechat/userInvoice/electron/generate', {
        openId: this.getLocalStorage('openId'), // 
        buyername: state.buyername, // 
        taxnum: state.taxnum, // 
        ordertotal: state.countMoney, // 
        email: state.email, // 
        accountType: state.accountType, // 
        account: state.account, // 
        address: state.address, // 
        telephone: state.telephone, // 
        message: state.message, // 
        orderUuids: state.PicktripList.toString(), // 
        ticketType: state.ticketType // 
      }).then((data) => {
        resolve(data)
      }).catch((err) => {
        reject(err)
      })
    })
    return promise
  },
  // 
  post_paperinvoice () {
    let promise = new Promise((resolve, reject) => {
      axios.post('/wechat/userInvoice/paper/generate', {
        openId: this.getLocalStorage('openId'),
        header: state.buyername, // 
        taxNumber: state.taxnum, // 
        money: state.countMoney, // 
        content: '',
        headerType: state.accountType, // 
        bankAccount: state.account, // 
        regAddress: state.address, // 
        regMobile: state.telephone, // 
        remark: state.message, // 
        orderUuids: state.PicktripList.toString(),
        type: state.ticketType, // 
// 
        recipient: state.post_name, // 
        mobile: state.post_mobile, // 
        area: state.post_area, // 
        detailAddress: state.post_address, // 
        freightType: (state.countMoney >= 300) ? 2 : 1 // 
      }).then((data) => {
        resolve(data)
      }).catch((err) => {
        reject(err)
      })
    })
    return promise
  }
}



